.gr-o-wrapper-stat{
	margin-bottom: 70px;
}
.gr-o-main-item p{
	font-size: 70px;
}
.gr-o-main-item span{
	font-size: 18px;
}
.gr-o-main-circle{
	top: -45px;
    right: 10px;
    & span{
    	color: $gr-o-main;
    }
}
.gr-o-main-item{
	width: 225px;
}
.gr-o-main-stat{
	background: $gr-o-main;
}
.gr-o-features-item img{
	fill:#EBF4FA;
}
.gr-o-map-subtitle{
	color: #34454E;	
	font-family: Roboto;	
	font-size: 30px;	
	font-weight: bold;	
	letter-spacing: 0.61px;	
	line-height: 35px;	
	text-align: center;
	margin-bottom: 70px;
}
.gr-o-wrapper-map{
	padding: 100px 0px;
}
.gr-o-features-item{
	display: flex;
	flex-direction: column;
	position: relative;
	margin-bottom: 15px;
}
.gr-o-features-item img{
	width:213px;
}
.gr-o-wrapper-features-stats{
	margin-bottom: 70px;
}
.gr-o-features-item-number{
	position: absolute;
	bottom: -20px;
	right: 100px;
	color: #7B57A0;	
	font-family: Roboto;	
	font-size: 100px;	
	font-weight: bold;	
	letter-spacing: 0.79px;	
	line-height: 117px;	
	text-align: center;
}
.gr-o-features-item-name{
	width: 80%;
	color: #34454E;	
	font-family: Roboto;	
	font-size: 24px;	
	letter-spacing: 0.79px;	
	line-height: 28px;	
	text-align: center;
}
.gr-o-spesnumber{
	right: 140px;
}
.gr-o-wrapper-map_svg{
	margin-bottom: 100px;
}
.gr-o-map-subtitle-stats{
	color: #34454E;
	font-family: Roboto;
	font-size: 30px;
	font-weight: bold;
	letter-spacing: 1.16px;
	line-height: 39px;
	text-align: center;
	margin-bottom: 70px;
}
.gr-o-subtitle-all_stats{
	color: #34454E;
	font-family: Roboto;
	font-size: 30px;
	font-weight: bold;
	letter-spacing: 1.16px;
	line-height: 39px;
	margin-bottom: 45px;
	text-align: center;
}
/*table*/
.gr-o-table-stats-main{
	width: 215px;
}
.gr-o-table-stats-header{
	border-radius: 8px;
	background-color: #7B57A0;
	box-shadow: -2px -1px 9px 0 rgba(0,0,0,0.17);
	/**/
	color: #FFFFFF;
	font-family: Roboto;
	font-size: 15px;
	font-weight: bold;
	letter-spacing: 1.04px;
	line-height: 18px;
	text-align: center;
	height: 91px;
}
.gr-o-table-stats-special{
	width: 263px;
	&::before{
		content:none;
	}
	& > a{
		position: absolute;
		top: 0;
		right: 15px;
	}
}
.gr-o-table-stats-end{
	width: 220px;
	&::before{
		content:none;
	}
	& > a{
		position: absolute;
		top: 0;
		right: 15px;
	}
}
.gr-o-table-stats-start{
	width: 179px;
	display: flex;
	align-items: center;
	justify-content: center;
	& > a{
		position: absolute;
		top: 0;
		right: 15px;
	}
	&::before{
		content:none;
	}
}
.gr-o-table-stats-name{
	width: 203px;
	border-radius: 8px;
	background-color: #7B57A0;
	box-shadow: -2px -1px 9px 0 rgba(0,0,0,0.17);
	/**/
	color: #FFFFFF;
	font-family: Roboto;
	font-size: 15px;
	font-weight: 500;
	letter-spacing: 1.04px;
	line-height: 18px;
	text-align: center;
	margin-right: 14px;
	&::before{
		content:none;
	}
}
.gr-o-table-stats-input{
	width: 210px;
	margin:0px 7px;
	border: 0;
    height: 55px;
    padding: 10px;
    background: #fff;
    box-shadow: -2px -1px 9px 0 rgba(0, 0, 0, 0.17);
    border-radius: 8px;
    & > div{
		display: flex;
		justify-content: center;
		height: 100%;
		align-items: center;
		color: #000000;
		font-family: Roboto;
		font-size: 15px;
		letter-spacing: 1.04px;
		line-height: 18px;
		text-align: center;
	}
	&last{
		width: 260px;
		margin:0px 7px;
		border: 0;
	    height: 55px;
	    padding: 10px;
	    background: #fff;
	    box-shadow: -2px -1px 9px 0 rgba(0, 0, 0, 0.17);
	    border-radius: 8px;
	    & > div{
			display: flex;
			justify-content: center;
			height: 100%;
			align-items: center;
	    }
	}
}
.gr-o-table-stats-line{
	justify-content: flex-start;
}
.gr-o-table-two-input{
	width: 210px;
}
.gr-o-table-one-input{
	width: 167px;
}
.gr-o-table-stats-input_prossent{
	color: #7B57A0;	
	font-family: Roboto;	
	font-size: 15px;	
	letter-spacing: 1.04px;	
	line-height: 18px;	
	margin-left: 5px;
	text-align: center;
}
.gr-o-page-form-stats{
	margin-bottom: 15px;
}
.gr-o-table-stats-wrapper-all{
	text-align: right;
	margin-bottom: 15px;
	& > a{
		font-family: Roboto;
		font-size: 15px;
		letter-spacing: 0.83px;
		line-height: 18px;
		text-align: center;
		color:$gr-o-main;
		text-decoration: underline;
	}
}
.gr-o-table-stats-wrapper-download{
	text-align: right;
	& > img{
		width: 20px;
		margin-right: 15px;
	}
	& > a{
		font-family: Roboto;
		font-size: 15px;
		letter-spacing: 0.83px;
		line-height: 18px;
		text-align: center;
		color:$gr-o-main;
		text-decoration: underline;
	}
}